import { registerDecorators as _registerDecorators } from "lwc";
import _tmpl from "./deployMessages.html";
import { registerComponent as _registerComponent } from "lwc";
import { LightningElement } from 'lwc'; // import * as fakeData from './__tests__/fakeData.json';

class DeployMessages extends LightningElement {
  constructor(...args) {
    super(...args);
    this.deployId = void 0;
    this.results = {
      complete: false,
      mainUser: {},
      herokuResults: [],
      errors: [],
      commandResults: []
    };
  }

  get resultsOutput() {
    return JSON.stringify(this.results);
  }

  deleteOrg(e) {
    console.log('delete called');
    e.preventDefault();
    e.stopPropagation();
    const xhttp = new XMLHttpRequest();
    xhttp.open('POST', '/delete', true);
    xhttp.setRequestHeader('Content-type', 'application/json');

    xhttp.onreadystatechange = function () {
      if (xhttp.readyState === 4 && xhttp.status === 302) {
        console.log(xhttp.response);
        console.log(xhttp.status);
        console.log(xhttp.responseText);
        window.location = xhttp.responseText;
      }
    };

    xhttp.send(JSON.stringify({
      username: this.results.mainUser.username
    }));
    return false;
  }

  handleMessage(msg) {
    console.log(msg);
    this.results = msg.detail;
  }

}

_registerDecorators(DeployMessages, {
  publicProps: {
    deployId: {
      config: 0
    }
  },
  track: {
    results: 1
  }
})

export default _registerComponent(DeployMessages, {
  tmpl: _tmpl
});